'use client';

import Link from 'next/link';
import { Search, Bookmark, User } from 'lucide-react';
import { useState, useEffect } from 'react';

export default function Navbar() {
    const [scrolled, setScrolled] = useState(false);

    useEffect(() => {
        const handleScroll = () => {
            setScrolled(window.scrollY > 20);
        };
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    return (
        <nav
            className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${scrolled ? 'bg-[#0F2027]/80 backdrop-blur-md shadow-lg' : 'bg-transparent'
                }`}
        >
            <div className="max-w-7xl mx-auto px-6 h-16 flex items-center justify-between">
                {/* Logo */}
                <div className="flex-shrink-0">
                    <Link href="/" className="text-2xl font-bold tracking-tight text-white hover:opacity-90 transition-opacity">
                        Kyno<span className="text-[#2C5364]">+</span>
                    </Link>
                </div>

                {/* Navigation Links */}
                <div className="hidden md:flex items-center space-x-8 text-sm font-medium text-gray-200">
                    <Link href="/" className="text-white hover:text-white border-b-2 border-white pb-1">Home</Link>
                    <Link href="/series" className="hover:text-white transition-colors">Series</Link>
                    <Link href="/movies" className="hover:text-white transition-colors">Movies</Link>
                    <Link href="/animes" className="hover:text-white transition-colors">Animes</Link>
                    <Link href="/live" className="hover:text-white transition-colors">TV ao Vivo</Link>
                </div>

                {/* Icons */}
                <div className="flex items-center space-x-6 text-gray-200">
                    <button className="hover:text-white transition-colors">
                        <Search size={20} />
                    </button>
                    <button className="hover:text-white transition-colors">
                        <Bookmark size={20} />
                    </button>
                    <button className="hover:text-white transition-colors">
                        <User size={20} />
                    </button>
                </div>
            </div>
        </nav>
    );
}
